import React, { useEffect, useState } from "react";
import {
  Button,
  IconButton,
  Snackbar,
  SnackbarContent,
  TextField,
  Typography,
} from "@material-ui/core";

import { checkAuth } from "../actions";
import { connect } from "react-redux";
import { Close } from "@material-ui/icons";
import api from "../api/api";
import { green, red } from "@material-ui/core/colors";

const Registration = ({
  checkAuth,
  setOpenRegistrationDialog,
  setOpenLoginDialog,
}) => {
  const [status, setStatus] = useState(null);
  const [toast, setToast] = useState(false);
  const [message, setMessage] = useState(null);
  const [wait, setWait] = useState(false);
  const [formValues, setFormValues] = useState({
    name: "",
    phone: "",
    email: "",
    address: "",
    password: "",
  });

  useEffect(() => {
    return () => {
      setFormValues({
        name: "",
        phone: "",
        email: "",
        address: "",
        password: "",
      });
    };
  }, []);

  const submit = async (e) => {
    e.preventDefault();
    try {
      setWait(true);
      const response = await api.post(`/api/customer`, formValues);
      localStorage.setItem("mom-customer", response.data.token);
      setFormValues({
        name: "",
        phone: "",
        email: "",
        address: "",
        password: "",
      });
      checkAuth();
      setWait(false);
      setStatus(green[700]);
      setMessage("Registration Completed Successfully");
      setOpenRegistrationDialog(false);
      return setToast(true);
    } catch (err) {
      setWait(false);
      setStatus(red[700]);
      setMessage("Registration failed. Something went wrong");

      return setToast(true);
    }
  };
  return (
    <React.Fragment>
      <div style={{ display: "flex", justifyContent: "center" }}>
        <img
          alt="registration"
          src="/logo512.png"
          style={{ height: 80, width: 80 }}
        />
      </div>
      <form onSubmit={submit}>
        <TextField
          required
          fullWidth
          variant="outlined"
          autoFocus
          margin="normal"
          size="small"
          label="Name"
          value={formValues.name}
          onChange={(e) =>
            setFormValues({ ...formValues, name: e.target.value })
          }
        />
        <TextField
          required
          fullWidth
          variant="outlined"
          type="number"
          margin="normal"
          size="small"
          label="Phone Number"
          value={formValues.phone}
          onChange={(e) =>
            setFormValues({ ...formValues, phone: e.target.value })
          }
        />
        <TextField
          required
          fullWidth
          variant="outlined"
          autoFocus
          type="email"
          margin="normal"
          size="small"
          label="Email"
          value={formValues.email}
          onChange={(e) =>
            setFormValues({ ...formValues, email: e.target.value })
          }
        />
        <TextField
          required
          fullWidth
          variant="outlined"
          autoFocus
          margin="normal"
          size="small"
          label="Address (Locality, City, District, State)"
          value={formValues.address}
          onChange={(e) =>
            setFormValues({ ...formValues, address: e.target.value })
          }
        />
        <TextField
          required
          type="password"
          fullWidth
          variant="outlined"
          margin="normal"
          size="small"
          label="Password"
          value={formValues.password}
          onChange={(e) =>
            setFormValues({ ...formValues, password: e.target.value })
          }
        />
        <Typography align="center" style={{ marginTop: 40 }}>
          Already have an account?{" "}
          <Button
            color="primary"
            onClick={() => {
              setOpenRegistrationDialog(false);
              setOpenLoginDialog(true);
            }}
          >
            Login Now
          </Button>
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            marginTop: 40,
          }}
        >
          <Button
            disabled={wait}
            type="submit"
            variant="contained"
            color="primary"
          >
            Register
          </Button>
          <Button
            style={{ marginLeft: 10 }}
            type="cancel"
            onClick={() => {
              setFormValues({
                name: "",
                phone: "",
                email: "",
                address: "",
                password: "",
              });
              setOpenRegistrationDialog(false);
            }}
            variant="contained"
            color="secondary"
          >
            Close
          </Button>
        </div>
      </form>

      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        open={toast}
        autoHideDuration={3000}
        onClose={() => {
          setToast(false);
        }}
      >
        <SnackbarContent
          style={{ backgroundColor: status }}
          message={message}
          onClose={() => {
            setToast(false);
          }}
          action={
            <IconButton
              key="close"
              color="inherit"
              onClick={() => setToast(false)}
            >
              <Close />
            </IconButton>
          }
        ></SnackbarContent>
      </Snackbar>
    </React.Fragment>
  );
};

export default connect(null, { checkAuth })(Registration);
